<template>
  <div :class="$style.mixin">
    <header :class="{[$style.header]: true, [$style.fixed]: fixed}" :title="title"  class="van-hairline--bottom">
      <div :class="$style.left" @click="click">
        <slot name="left">
          <i class="iconfont icon-back" :class="$style.back"></i>
        </slot>
      </div>
      <div :class="$style.title" @click="$emit('click-title')">
        <slot>
          {{title}}
        </slot>
      </div>
      <div :class="$style.right" @click="$emit('click-right')">
        <slot name="right"/>
      </div>
    </header>
    <div v-if="fixed" :class="$style.clear"></div>
  </div>
</template>

<script>
export default {
  name: 'v-header',
  props: {
    title: {
      type: String,
      required: false
    },
    fixed: {
      type: Boolean,
      default: true
    },
    opacity: {
      type: Number,
      default: 0.8
    },
    back: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    click (e) {
      if (this.back) {
        this.$router.go(-1)
      } else {
        this.$emit('click-left')
      }
    }
  }
}
</script>

<style module>
.header{
  width: 100%;
  height: 46px;

  line-height: 46px;
  text-align: center;
  background-color: #ffffff;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  box-shadow: 0px 0px 5px #bbbbbb;
}
.fixed{
  position: fixed;
  top: 0;
  z-index: 99;
}
.title{
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
  box-sizing: border-box;
  padding: 0 10px;
}
.left, .right{
  width: 50px;
  height: 100%;
}
.clear{
  width: 100%;
  height: 46px;
}
.back{
  color: #3388ff;
  font-weight: 100;
  font-size: 20px;
}
.mixin{
  font-size: 16px;
  font-weight: normal;
}
</style>



