<template>
  <div :class="$style.author">
    <router-link tag="div" :to="to" :class="$style.avatar" :style="styleObj"></router-link>
    <router-link tag="div" :to="to" :class="$style.loginname">{{author.loginname}}</router-link>
    <div :class="$style.more" @click="$emit('click-right')">
      <slot/>
    </div>
  </div>
</template>

<script>
const keys = ['loginname', 'avatar_url']
export default {
  name: 'v-author',
  props: {
    author: {
      required: true,
      validator (v) {
        if (typeof v !== 'object') {
          return false
        }
        return keys.every((key) => typeof v[key] !== 'undefined')
      }
    }
  },
  computed: {
    styleObj () {
      return {
        backgroundImage: `url(${this.author.avatar_url})`
      }
    },
    to () {
      return `/user/${this.author.loginname}`
    }
  },
  methods: {
    click (e) {
      console.log(e)
    }
  }
}
</script>

<style module>
.author{
  width: 100%;
  height: 46px;
  line-height: 46px;

  display: flex;
  flex-direction: row;
  align-items: center;
}
.more{
  min-width: 40px;
  height: 100%;
}
.avatar{
  width: 40px;
  height: 40px;

  background-size: cover;
  background-position: center center;
  background-size: cover;
  vertical-align: middle;
  border-radius: 50%;
}
.loginname{
  flex: 1;
  text-indent: 1em;
}
</style>



