<template>
  <v-panel :class="$style.topic">
    <v-author slot="title" :author="topicInfo.author"/>
    <router-link tag="div" :to="topicLink" :class="$style.title">{{topicInfo.title}}</router-link>
    <router-link tag="div" :to="replyLink" slot="footer" :class="$style.footer" class="van-hairline--top">
      <span :class="$style.item" class="van-hairline--right"><i class="icon-attention iconfont"></i> {{topicInfo.visit_count}}</span>
      <span :class="$style.item" class="van-hairline--right"><i class="icon-mark iconfont"></i> {{topicInfo.reply_count}}</span>
      <span :class="$style.item"><i class="icon-time iconfont"></i> {{topicInfo.create_at | time}}</span>
    </router-link>
  </v-panel>
</template>

<script>
import Panel from '@/base/Panel'
import Author from './Author'
export default {
  name: 'v-topic',
  props: {
    topicInfo: {
      type: Object,
      required: true
    }
  },
  computed: {
    topicLink () {
      return `/topic/${this.topicInfo.id}#title`
    },
    replyLink () {
      return `/topic/${this.topicInfo.id}#reply`
    }
  },
  components: {
    [Panel.name]: Panel,
    [Author.name]: Author
  }
}
</script>

<style module>
.topic{
  box-sizing: border-box;
  padding: 10px 10px 0 10px;
  margin-bottom: 20px;
  background-color: #ffffff;
  box-shadow: 0 0 10px #cccccc;
}
.footer{
  width: 100%;
  height: 46px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: stretch;
  text-align: center;
  line-height: 46px;
}
.item{
  flex: 1;
}
.title{
  padding: 20px;
  line-height: 120%;
  min-height: 50px;
}
</style>



