<template>
  <div :class="$style.panel">
    <div :class="$style.title">
      <slot name="title"/>
    </div>
    <div :class="$style.content">
      <slot/>
    </div>
    <div :class="$style.footer">
      <slot name="footer"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'v-panel'
}
</script>

<style module>
.panel{
  width: 100%;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: stretch;
}
.content{
  flex: 1;
}
</style>



