<template>
  <div :class="$style.bottom_line">
    <slot>
      {{text}}
    </slot>
  </div>
</template>

<script>
export default {
  name: 'v-bottom-line',
  props: {
    text: {
      type: String,
      default: '回到顶部。下拉刷新!'
    }
  }
}
</script>

<style module>
.bottom_line{
  width: 100%;
  height: 46px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-size: 14px;
  color: #888;
  background-color: #eee;
}
</style>



