<template>
  <div>
    <v-header title="Node.js中文社区" :class="$style.mixin" @click-right="nomore" :back="false">
      <span slot="left"></span>
      <i class="iconfont icon-more" :class="$style.back" slot="right"></i>
    </v-header>
    <van-actionsheet v-model="show" :actions="actions" cancel-text="取消">
    </van-actionsheet>
  </div>
</template>

<script>
import { Actionsheet } from 'vant'
import HeaderVue from '../../base/Header.vue'
export default {
  name: 'x-header',
  components: {
    [HeaderVue.name]: HeaderVue,
    [Actionsheet.name]: Actionsheet
  },
  data () {
    return {
      show: false,
      actions: [
        {
          name: 'Github',
          callback: this.onClick
        },
        {
          name: '...',
          loading: true
        }
      ]
    }
  },
  methods: {
    nomore () {
      this.show = true
    },
    onClick (e) {
      window.location.href = 'https://github.com/alexlees'
    }
  }
}
</script>

<style module>
.back{
  color: #3388ff;
  font-weight: 100;
  font-size: 20px;
}
.mixin{
  font-size: 16px;
  font-weight: normal;
}
</style>



