<template>
  <div :class="$style.home">
    <x-header/>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <div :class="$style.scroll">
        <div v-if="topics.length">
          <v-topic v-for="(item, index) in topics" :key="index" :topicInfo="item"></v-topic>
        </div>
        <v-bottom-line/>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { PullRefresh } from 'vant'
import Header from './Header.vue'
import Topic from '@/components/Topic'
import BottomLine from '@/base/BottomLine'
import { fetchTopics } from '../../util/api'
export default {
  name: 'page-home',
  components: {
    [Header.name]: Header,
    [PullRefresh.name]: PullRefresh,
    [BottomLine.name]: BottomLine,
    [Topic.name]: Topic
  },
  data () {
    return {
      scrollY: 0,
      page: 1,
      topics: [],
      isLoading: false
    }
  },
  methods: {
    async pushTopic (unshift = false) {
      this.isLoading = true
      try {
        const res = await fetchTopics({page: this.page})
        if (res.success) {
          if (unshift) {
            this.topics.unshift(...res.data)
          } else {
            this.topics.push(...res.data)
          }
          if (this.topics.length >= 50) {
            this.topics.splice(-10)
          }
          this.page += 1
        }
      } catch (error) {
        console.error(error)
      }
    },
    async onRefresh (e) {
      await this.pushTopic(true)
      setTimeout(() => {
        this.isLoading = false
      }, 500)
    },
    async initTopic () {
      this.isLoading = true
      await this.pushTopic
      this.isLoading = false
    }
  },
  async created () {
    this.$toast.loading({
      mask: true,
      message: '初始化中...'
    })
    await this.pushTopic()
    this.$toast.clear()
  }
}
</script>

<style module>
.scroll{
  width: 100%;
  height: auto;
  background-color: #eeeeee;
}
.home{
  background-color: #ffffff;
}
</style>



